$pagination-color:                  var(--#{$prefix}secondary-color);
$pagination-bg:                     var(--#{$prefix}secondary-bg);
$pagination-border-radius:          var(--#{$prefix}border-radius);
$pagination-border-width:           var(--#{$prefix}border-width);
